.process {
  position: relative;
  width: 260px;
  margin: 0 auto;
  margin-bottom: 58px;
  padding-top: 25px;
  padding-bottom: 30px;
  border: 1px solid #ececec;

  &:hover,
  &:focus {
    box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.1);
  }

  &:active {
    box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.1);
  }
}

.process__header {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 10px;
    left: 34px;
    width: 32px;
    height: 22px;
    background-image: url("data:image/svg+xml,%3Csvg version='1.1' id='Layer_1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' x='0px' y='0px' width='32px' height='32px' viewBox='0 0 32 32' enable-background='new 0 0 32 32' xml:space='preserve'%3E%3Cg fill='%2363d1bb' transform='translate(0 192)'%3E%3Cpath d='M26-177l6-4v16l-6-4V-177z M24-177v8c0,2.209-1.791,4-4,4H4c-2.209,0-4-1.791-4-4v-8c0-2.209,1.791-4,4-4h10 c0-1.105,0.895-2,2-2v-2H5c-0.553,0-1-0.447-1-1s0.447-1,1-1h12c0.553,0,1,0.447,1,1v3c1.105,0,2,0.895,2,2 C22.209-181,24-179.209,24-177z M12-177H4v8h8V-177z M20-176c0-0.553-0.447-1-1-1h-4c-0.553,0-1,0.447-1,1s0.447,1,1,1h4 C19.553-175,20-175.447,20-176z'/%3E%3C/g%3E%3C/svg%3E%0A");
    background-repeat: no-repeat;
    background-position: center;
  }
}

.process__title {
  font-size: 17px;
  font-weight: bold;
  line-height: 1.41;
  letter-spacing: 1.2px;
  margin: 0;
  margin-bottom: 26px;
  padding-left: 90px;
}

.process__image {
  margin-bottom: 25px;
}

.process__button {
  &--play {
    position: absolute;
    top: 134px;
    left: 80px;
    display: inline-block;
    width: 101px;
    height: 77px;
