-0.021,0.371-0.041,0.58-0.062 c1.836-0.186,4.608-0.468,7.186-3.335l0.026-0.029c1.825-1.974,2.853-6.928,3.075-8.552c0.059-0.514,0.926-8.358,0.926-16.08 v-7.699c-0.023-7.76-0.896-15.627-0.951-16.109c-0.32-2.382-1.434-6.789-3.066-8.539l-0.035-0.038 c-2.577-2.868-5.351-3.149-7.186-3.336c-0.209-0.021-0.402-0.041-0.58-0.062c-13.066-0.998-32.834-1.011-33.033-1.011h-0.051 c-0.198,0-19.967,0.013-33.033,1.011c-0.177,0.021-0.371,0.041-0.58,0.062c-1.835,0.187-4.607,0.468-7.186,3.336l-0.019,0.021 c-1.828,1.993-2.855,6.924-3.082,8.552c-0.062,0.591-0.875,8.399-0.875,16.089v7.699c0,7.749,0.871,15.598,0.926,16.083 c0.32,2.381,1.433,6.789,3.066,8.539l0.035,0.039c2.322,2.584,5.303,2.975,7.479,3.259 C264.788,452.812,265.246,452.872,265.67,452.952z'/%3E%3C/g%3E%3C/g%3E%3Cg%3E%3Cpolygon fill='%23FFFFFF' points='285.733,403.138 285.733,438.846 315.48,420.992 '/%3E%3C/g%3E%3C/svg%3E%0A");
    background-repeat: no-repeat;

    &:hover,
    &:focus {
      transform: scale(1.2);
    }

    &:active {
      opacity: 0.8;
      transform: scale(1);
    }
  }

  &--add {
    display: inline-block;
    width: 150px;
    text-decoration: none;
    font-size: 17px;
    font-weight: bold;
    line-height: 1.18;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: #444444;
    border: solid 2px #444444;
    margin-left: 33px;
    padding-top: 15px;
    padding-right: 15px;
    padding-bottom: 13px;
    padding-left: 25px;

    &:hover {
      color: #ffffff;
      background-color: #444444;
    }

    &:active {
      color: rgba(255, 255, 255, 0.3);
    }
  }
}

.process__description {
  width: 184px;
  font-size: 13px;
  font-weight: normal;
  line-height: 1.85;
  margin: 0 34px;
  margin-bottom: 24px;
}

@media (min-width: 768px) {
  .process {
    width: 668px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    margin-bottom: 60px;
    padding: 0;
    padding-bottom: 44px;
  }

  .process__header {
    order: 2;
    padding-right: 40px;
    padding-left: 40px;
    padding-bottom: 32px;

    &::before {
      top: 0;
      left: 42px;
    }
  }

  .process__title {
    font-size: 20px;
    line-height: 1.2;
    padding-left: 65px;
    margin: 0;
  }

  .process__image {
    width: 668px;
    height: 455px;
    order: 1;
    margin-bottom: 35px;
  }

  .process__button {
    &--play {
      top: 188px;
      left: 282px;
    }

    &--add {
      width: 500px;
      margin: 0 auto;
      text-align: center;
      padding: 15px 41px;
      order: 4;
    }
  }

  .process__description {
    width: 583px;
    font-size: 15px;
    line-height: 2;
    margin: 0 auto;
    margin-bottom: 35px;
    order: 3;
  }
}

@media (min-width: 1150px) {
  .process {
    position: relative;
    width: 1050px;
    margin: 0 auto;
    margin-bottom: 103px;
    flex-direction: column;
    padding-left: 759px;
    flex-wrap: wrap;
    padding-bottom: 59px;
    box-sizing: border-box;
  }

  .process__header {
    width: 230px;
    margin: 0;
    margin-bottom: 33px;
    padding: 0;
    padding-top: 95px;

    &::before {
      top: 47px;
      left: 1px;
    }
  }

  .process__title {
    width: 150px;
    margin: 0;
    padding-left: 0;
  }

  .process__image-wrapper {
    width: 682px;
    position: absolute;
    top: 0;
    left: 0;
  }

  .process__image {
    width: 682px;
    margin: 0;
  }

  .process__description {
    width: 235px;
    margin: 0;
    margin-bottom: 46px;
  }

  .process__button {
    &--play {
      top: 188px;
      left: 290px;
    }

    &--add {
      width: 150px;
      margin: 0;
      margin-left: 2px;
      padding-left: 38px;
      padding-right: 38px;
    }
  }
}
